import React from 'react';
import {Col, Container, Row} from "react-bootstrap";

const Crop = ({children}) => {
    return (
        <Container fluid className="crop">
            <Row className="pt-5">
                <Col className="py-5">
                    <h1 className="text-center text-light text-uppercase">
                        {children}
                    </h1>
                </Col>
            </Row>
        </Container>
    );
};

export default Crop;