import React from 'react';
import {Container, Row} from "react-bootstrap";
import Calculate from '../../assets/images/icons/calculate.svg'
import History from '../../assets/images/icons/history.svg'
import Information from '../../assets/images/icons/information.svg'
import MainCard from "../UI/Cards/MainCard";

const Main = () => {
    const cards = [
        {title: '', text: ' ', src: Information, link: '/information'},
        {title: '', text: ' ', src: History, link: '/history'},
        {title: '', text: ' ', src: Calculate, link: '/report'}
    ];

    return (
        <main className="pt-2 pb-2">
            <Container fluid>
                <Row>
                    {cards.map((card, ind) => (
                        <MainCard
                            key={ind}
                            title={card.title}
                            text={card.text}
                            link={card.link}
                            variant="top"
                            src={card.src}
                            width="40px"
                            height="40px"
                            className="pt-2"
                        />
                    ))}
                </Row>
            </Container>
        </main>
    );
};

export default Main;