import React from 'react';
import Infinity from './Preloader.module.scss'
import './PreloaderJS'

const Preloader = () => {
    return (
        <React.Fragment>
            <div className={Infinity.preloaderWrapper}>
                <div className={Infinity.preloader}>
                    <div className={Infinity.dotContainer}>
                        <div className={Infinity.dot}></div>
                        <div className={Infinity.dot}></div>
                        <div className={Infinity.dot}></div>
                    </div>
                    <div className={Infinity.dotContainer}>
                        <div className={Infinity.dot}></div>
                        <div className={Infinity.dot}></div>
                        <div className={Infinity.dot}></div>
                    </div>
                    <div className={Infinity.dotContainer}>
                        <div className={Infinity.dot}></div>
                        <div className={Infinity.dot}></div>
                        <div className={Infinity.dot}></div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    );
};

export default Preloader;