import React from 'react';
import {Container} from "react-bootstrap";
import {Link} from "react-router-dom";

const Footer = () => {
    return (
        <Container fluid className="mt-auto">
            <footer className="row py-5 border-top">
                <div className="col-sm-12 col-md-2 py-1 py-sm-4 py-md-5">
                    <p className="text-center text-sm-center text-md-center text-lg-start">
                        <Link to="/home" className="navbar-brand"><span>47</span></Link>
                        <br className="d-md-none"/>
                        <small>2021</small>
                    </p>
                </div>

                <div className="col d-none d-sm-none d-md-block">

                </div>

                <div className="col col-sm-12 col-md-2 text-sm-center text-md-start">
                    <h5 className="additional_color fw-bolder text-uppercase text-white shadow--clear"></h5>
                    <ul className="nav flex-column text-light text-light shadow--clear">
                        <li className="nav-item mb-2"><Link to="/information" className="footer__link"></Link></li>
                        <li className="nav-item mb-2"><Link to="/history" className="footer__link"></Link></li>
                    </ul>
                </div>

                <div className="col text-sm-center text-md-start">
                    <h5 className="additional_color fw-bolder text-uppercase text-white shadow--clear"></h5>
                    <ul className="nav flex-column text-light shadow--clear">
                        <li className="nav-item mb-2"><Link to="/report" className="footer__link"></Link></li>
                    </ul>
                </div>
            </footer>
        </Container>
    );
};


export default Footer;