import React, {useState} from 'react';
import {Container, Navbar, Nav, NavDropdown} from "react-bootstrap";
import {Link} from "react-router-dom";
import logo from '../../assets/images/logo/logo_dark.png';

const Menu = () => {
    const [expanded, setExpanded] = useState(false);
    return (
        <header className="fixed-top navigation">
            <Navbar expanded={expanded} bg="white" expand="lg">
                <Container fluid>
                    {/*<span>47</span>*/}
                    <Link to="/home" className="navbar-brand"><img src={logo} width={55} height={55} alt=""/></Link>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" onClick={() => setExpanded(expanded ? false : "expanded")} />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="me-auto">
                            <Link to="/home" data-rr-ui-event-key="/" className="nav-link" onClick={() => setExpanded(false)}></Link>
                            <NavDropdown title="" id="basic-nav-dropdown">
                                <Link to="/information" data-rr-ui-dropdown-item className="dropdown-item" onClick={() => setExpanded(false)}></Link>
                                <Link to="/history" data-rr-ui-dropdown-item className="dropdown-item" onClick={() => setExpanded(false)}></Link>
                            </NavDropdown>
                            <NavDropdown title="" id="basic-nav-dropdown">
                                <Link to="/report" data-rr-ui-dropdown-item className="dropdown-item" onClick={() => setExpanded(false)}></Link>
                            </NavDropdown>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
        </header>
    );
};

export default Menu;